body {
  margin: 0;
  padding: 0;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  max-width: 1400px;
  margin: auto;
}

.nav {
  display: flex;
  justify-content: space-between;
}

h1{
  font-size: 55px;
  margin: 5px;
  padding-left: 25px;
}

.search-box{
  padding-left: 30px;
  padding-top: 15px;
  position: absolute;
  right: 0;
  bottom: -35px;
}

.search-container {
  position: relative;

}

.search-box input {
  padding: 5px;
  font-size: 18px;

}

.search-box button {
  padding: 5px;
  font-size: 18px;
  font-weight: bold;
}

ul{
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}



.nav li {
  padding: 10px 30px;
  
}

.nav a {
  text-decoration: none;
  color: black;
  font-size: 25px;
  padding: none;
}

.news-articles {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5px;
  grid-template-rows: auto;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  padding-top: 45px;

}

.news-content {
  max-width: 1000px;
  padding: 30px 30px;;
  padding: auto;
  align-content: center;
  background-color: rgba(128, 128, 128, 0.247);
  position: relative;
}

.news-content p {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.news-articles a{
  color: black;
  text-decoration: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.date{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.source-date{
  position: absolute;
  bottom: 30px;
}

.footer {
  padding-left: 30px;
}

.footer a {
  text-decoration: none;
  color: black;
}

.about-page{
  width: 1000px;
  margin: auto;
  padding-top: 40px;
}

